





































































































































.flex {
    margin: 17px 0;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .from_item {
        display: flex;
        align-items: center;
        width: 50%;
        margin-bottom: 30px;
        .from_lable {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #333333;
            text-align: right;
            width: 80px;
        }
        .from_content {
            width: 80px;
            margin-left: 24px;
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #666666;
        }
    }
}
